.card-container {
  background: orange;
  margin-top: 1rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  transition: all 0.2s ease-in-out;
  &:hover {
    transform: scale(1.1);
  }

  &__rating {
    display: flex;
    justify-content: flex-end;
    & span {
      font-size: 2rem;
      background: yellowgreen;
      border-radius: 2rem;
      height: 3rem;
      text-align: center;
      width: 3rem;
      position: relative;
      left: 1%;
      top: 5%;
    }
  }

  & img {
    max-width: 100%;
  }
  &__text {
    padding-left: 1rem;
    & h1 {
      font-size: 1.5rem;
    }

    & p {
      font-size: 1rem;
    }
  }
}
